@import url(https://fonts.googleapis.com/css2?family=Red+Hat+Display:wght@900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lexend:wght@100;200;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

@font-face {
  font-family: 'Geometos';
  src: local('Geometos'), url(/static/media/Geometos.f643a91a.ttf) format('opentype');
}

:root {
  --welcome-logo-size: max(100px, min(20vw, 20vh));
  --welcome-logo-word-size: calc(.2 * var(--welcome-logo-size));
}

.welcome-logo-container {
  position: absolute;
  left: 50%;
  top: 50%;
  -webkit-transform: translate(-50%, calc(-.5 * max(100px, min(20vw, 20vh))));
          transform: translate(-50%, calc(-.5 * max(100px, min(20vw, 20vh))));
  -webkit-transform: translate(-50%, calc(-.5 * var(--welcome-logo-size)));
          transform: translate(-50%, calc(-.5 * var(--welcome-logo-size)));
  width: max(100px, min(20vw, 20vh));
  width: var(--welcome-logo-size);
}

.welcome-logo {
  width: max(100px, min(20vw, 20vh));
  width: var(--welcome-logo-size);
  height: max(100px, min(20vw, 20vh));
  height: var(--welcome-logo-size);
  background-image: url(/static/media/GKP-2111.372ff579.svg);
  background-size: cover;
  /* filter: invert(21%) sepia(94%) saturate(0%) hue-rotate(171deg) brightness(91%) contrast(95%); */
}

.welcome-logo-word {
  font-family: 'Red Hat Display', sans-serif;
  font-size: calc(.2 * max(100px, min(20vw, 20vh)));
  font-size: var(--welcome-logo-word-size);
  text-align: center;
  letter-spacing: calc(-.05 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(-.05 * var(--welcome-logo-word-size));
  margin: 0;
  padding-top: calc((100vh - max(100px, min(20vw, 20vh))) / 4 - calc(.2 * max(100px, min(20vw, 20vh))) / 1.25);
  padding-top: calc((100vh - var(--welcome-logo-size)) / 4 - var(--welcome-logo-word-size) / 1.25);
  color: #3e3e3e;
  overflow: hidden;
}

.welcome-logo-word>.G {
  letter-spacing: calc(-.04 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(-.04 * var(--welcome-logo-word-size));
}

.welcome-logo-word>.r {
  letter-spacing: calc(-.03 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(-.03 * var(--welcome-logo-word-size));
}

.welcome-logo-word>.a {
  letter-spacing: calc(-.05 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(-.05 * var(--welcome-logo-word-size));
}

.welcome-logo-word>.k {
  letter-spacing: calc(-.1 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(-.1 * var(--welcome-logo-word-size));
}

.welcome-logo-word>.e {
  letter-spacing: calc(.1 * calc(.2 * max(100px, min(20vw, 20vh))));
  letter-spacing: calc(.1 * var(--welcome-logo-word-size));
}

.noScrollBar::-webkit-scrollbar {
  width: 0;
  height: 0;
}
